@import '~antd/dist/antd.css';

.ant-layout {
    height: 100vh;
}
.ant-layout-sider-children {
    background-color: #fff;
}

.site-page-header {
  border: 1px solid rgb(235, 237, 240);
  margin-bottom: 0.8em;
}

.trigger {
    padding: 0 24px;
    font-size: 18px;
    line-height: 64px;
    cursor: pointer;
    transition: color 0.3s;
  }
  
  .trigger:hover {
    color: #1890ff;
  }
  
  .logo {
    height: 32px;
    margin: 16px;
    background: rgba(255, 255, 255, 0.3);
  }
  
  .site-layout .site-layout-background {
    background: #fff;
  }